/* ############### LEAF MAIN NAV ################# */
.leaf-nav-icon {
  float: right;
  margin: 0.2rem 0.3rem 0.2rem 0;
  color: #00bde3;
}

.leaf-nav-icon-space {
  float: left;
  margin: 0;
}

#nav, #nav ul, #nav li {
  margin: 0;
  padding: 0;
  border: 0;
  list-style: none;
  box-sizing: border-box;
  padding-inline-start: 0;
}

#nav {
  position: relative;
}

@media screen and (min-width: 48.5rem) {
  #nav {
    display: block;
    float: right;
    clear: both;
    background-color: #252f3e;
    min-height: 32px;
    max-width: 100%;
    margin: 2.3rem 3.4rem 0 0;
  }

  #nav ul {
    z-index: 9999;
  }

  #nav li {
    position: relative;
  }

  #nav ul li a {
    text-decoration: none;
    height: 100%;
    display: block;
    padding: 8px;
    color: #f0f0ec;
  }
}
@media screen and (max-width: 48.4rem) {
  #nav {
    margin: 0;
    background: none;
    height: 1.6rem;
    width: 1.6rem;
    float: right;
    margin: 0.2rem 0.2rem;
  }

  #nav ul {
    position: relative;
  }

  #nav > ul > li.leaf-mob-menu {
    background-color: #274863;
    width: 12rem;
    padding: 0.3rem 0;
    margin-left: -10rem;
  }

  #nav > ul > li:not(#toggleMenu) > a {
    text-decoration: none;
    color: #f0f0ec;
    margin-left: 0.5rem;
  }

  #nav > ul > li#toggleMenu > a {
    text-decoration: none;
  }
}
@media screen and (min-width: 48.5rem) {
  #nav li {
    text-align: left;
  }
}
/* Any sub menu */
@media screen and (min-width: 48.5rem) {
  a + ul {
    position: absolute;
  }

  a + ul:not(.js-showElement) {
    display: none;
  }
}
@media screen and (max-width: 48.4rem) {
  a + ul {
    position: relative;
  }

  a + ul:not(.js-hideElement) {
    display: block;
  }
}
/* ////////////// Navigation Level One /////////////// */
#nav > ul, .fa {
  height: 100%;
}

#nav > ul > li {
  text-align: left;
  font-family: "Public Sans Regular";
}

@media screen and (min-width: 48.5rem) {
  #nav > ul > li {
    position: relative;
    float: left;
    font-size: 0.9rem;
    margin-left: 0.2rem;
  }
}
@media screen and (max-width: 48.4rem) {
  #nav > ul > li {
    font-size: 0.8rem;
    padding: 0.3rem 0;
  }
}
@media screen and (min-width: 48.5rem) {
  #nav > ul > li > a {
    background-color: #252f3e;
  }

  #nav > ul > li > a:hover,
#nav > ul > li > a:focus,
#nav > ul > li > a.js-openSubMenu {
    background-color: #274863;
    font-family: "Public Sans Bold";
  }

  #nav > ul > li:hover > a,
#nav > ul > li:focus > a {
    background-color: #274863;
  }

  #nav > ul > li:not(:last-child) {
    border-right: 0px solid #252f3e;
    border-bottom: none;
  }
}
#nav > ul > li:not(#toggleMenu):not(.js-showElement) {
  /* first level nav li except toggleMenu icon */
}

@media screen and (min-width: 48.5rem) {
  #nav > ul > li:not(#toggleMenu):not(.js-showElement) {
    display: inline-block;
  }
}
@media screen and (max-width: 48.4rem) {
  #nav > ul > li:not(#toggleMenu):not(.js-showElement) {
    display: none;
  }
}
@media screen and (min-width: 48.5rem) {
  #nav #toggleMenu {
    display: none;
  }
}
@media screen and (max-width: 48.4rem) {
  #nav #toggleMenu {
    font-size: 1.4rem;
    line-height: 1.4rem;
  }

  #nav #toggleMenu.js-open {
    width: 27px;
    height: 27px;
  }

  #nav #toggleMenu.js-open .fa-times {
    display: block;
    width: 27px;
    height: 27px;
    color: #fff;
  }

  #nav #toggleMenu.js-open .fa-bars {
    display: none;
  }

  #nav #toggleMenu.js-open a {
    width: 24px;
    height: 24px;
    display: block;
    text-decoration: none;
    margin-bottom: 0.8rem;
  }

  #nav #toggleMenu:not(.js-open) .fa-times {
    display: none;
  }

  #nav #toggleMenu:not(.js-open) .fa-bars {
    display: block;
    width: 27px;
    height: 27px;
    color: #fff;
    text-decoration: none;
  }

  span#toggleMenu-text {
    position: absolute;
    opacity: 0;
  }
}
/* ///////////// Second Level Dropdown //////////////// */
@media screen and (min-width: 48.5rem) {
  #nav > ul > li > ul {
    background-color: #274863;
    top: 37px;
    left: -2rem;
    width: 12rem;
    font-size: 0.8rem;
  }

  #nav > ul > li > ul.leaf-usernavmenu {
    left: -8rem;
  }

  #nav > ul > li > ul > li:hover,
#nav > ul > li > ul > li > a:hover,
#nav > ul > li > ul > li > a:focus,
#nav > ul > li > ul > li > a:focus-within {
    background-color: #2378c3;
    font-family: "Public Sans Bold";
  }
}
@media screen and (max-width: 48.4rem) {
  #nav > ul > li > ul {
    background-color: #2378c3;
  }

  #nav > ul > li > ul:not(.js-showElement) {
    display: none;
  }

  #nav > ul > li > ul > li {
    padding: 0.3rem 0;
  }

  #nav > ul > li > ul > li > a {
    color: #f0f0ec;
    text-decoration: none;
    margin-left: 0.5rem;
  }
}
/* //////////////// Third Level Dropdown ////////////////// */
@media screen and (min-width: 48.5rem) {
  #nav > ul > li > ul > li > ul {
    top: 0;
    left: -12rem;
    width: 12rem;
    font-size: 0.8rem;
    font-family: "Public Sans Regular";
    z-index: 9999;
  }
}
@media screen and (max-width: 48.4rem) {
  #nav > ul > li > ul > li > ul {
    width: 100%;
    position: relative;
  }

  #nav > ul > li > ul > li > ul:not(.js-showElement) {
    display: none;
  }
}
#nav > ul > li > ul > li > ul > li > a {
  background-color: #2378c3;
  color: #f0f0ec;
  padding: 4px;
  text-decoration: none;
}

#nav > ul > li > ul > li > ul > li > a:hover,
#nav > ul > li > ul > li > ul > li > a:focus {
  background-color: #2378c3;
  font-family: "Public Sans Bold";
}

/* Javascript classes */
#nav .js-hideElement {
  display: none;
}

#nav .js-showElement {
  display: block;
}

/* Fallback for users without javascript */
html.no-js li:hover > a + ul, html.no-js li:focus > a + ul {
  display: block;
}

@media screen and (max-width: 48.4rem) {
  html.no-js #nav:hover > ul > li:not(#toggleMenu),
html.no-js #nav:focus > ul > li:not(#toggleMenu) {
    display: block;
  }

  html.no-js #nav:hover li:hover > a + ul,
html.no-js #nav:hover li:focus > a + ul,
html.no-js #nav:focus li:hover > a + ul,
html.no-js #nav:focus li:focus > a + ul {
    display: block;
  }
}